      console.log(true);
    },
    btnClick:function(){
      alert('')
    },
    set1:function(){
      console.log('set1');
      this.$data.simple='simple'
    },
    set2:function(){
      console.log('set2');
      this.$data.simple='double';
    }
  },
  components: {
    'v-header': Header,
    'v-content':Content,
    'v-list': List,
    'v-checked': Checked,
    'v-item': ListItem,
    'v-footer': Footer,
    'footer-item': FooterItem
